<template>
    <div class="pos" id="pos">
        <el-row >
            <el-col :span="7" class="order-list" id="order-list">
                <el-tabs v-model="activeName2" type="border-card">
                    <el-tab-pane label="点餐" name="first">
                        <el-table
                        :data="tableData"
                        border
                        stripe
                        style="width: 100%">
                            <el-table-column
                            prop="goodsName"
                            label="商品名称">
                            </el-table-column>
                            <el-table-column
                            prop="count"
                            label="数量"
                            width="50">
                            </el-table-column>
                            <el-table-column
                            prop="price"
                            label="金额"
                            width="70">
                            </el-table-column>
                            <el-table-column
                            label="操作"
                            width="100" 
                            fixed="right">
                                <template slot-scope="scope">
                                    <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="addOrderList(scope.row)">添加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <p class="total-content">
                            <small>总量：</small>{{totalCount}}
                            <small>总金额：</small>{{totalMoney}}<small>元</small>
                        </p>
                        <div class="btn-group">
                            <el-button type="warning">挂单</el-button>
                            <el-button type="danger" @click="delAllGoods">删除</el-button>
                            <el-button type="success" @click="checkOut">结账</el-button>
                        </div> 
                    </el-tab-pane>
                    <el-tab-pane label="挂单" name="second">挂单</el-tab-pane>
                    <el-tab-pane label="外卖" name="third">外卖</el-tab-pane>
                </el-tabs>
            </el-col>
            <!--商品展示-->
            <el-col :span="17" class="goods-list" id="goods-list">
                <div class="often-goods">
                    <div class="title">常用商品</div>
                    <div class="often-goods-list">
                        <ul class="clear-float">
                            <li v-for="good in hotGoods" @click="addOrderList(good)">
                                <span v-text="good.goodsName"></span>
                                <span class="o-price">￥{{good.price}}元</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="goods-type">
                    <el-tabs  type="border-card">
                        <el-tab-pane label="汉堡">
                            <ul class='cookList clear-float'>
                                <li v-for="good in goodsList0" @click="addOrderList(good)">
                                    <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                                    <span class="foodName">{{good.goodsName}}</span>
                                    <span class="foodPrice">￥{{good.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="小食">
                            <ul class='cookList clear-float'>
                                <li v-for="good in goodsList1" @click="addOrderList(good)">
                                    <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                                    <span class="foodName">{{good.goodsName}}</span>
                                    <span class="foodPrice">￥{{good.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <ul class='cookList clear-float'>
                                <li v-for="good in goodsList2" @click="addOrderList(good)">
                                    <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                                    <span class="foodName">{{good.goodsName}}</span>
                                    <span class="foodPrice">￥{{good.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <ul class='cookList clear-float'>
                                <li v-for="good in goodsList3" @click="addOrderList(good)">
                                    <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                                    <span class="foodName">{{good.goodsName}}</span>
                                    <span class="foodPrice">￥{{good.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                
                    </el-tabs>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    name:'Pos',
    data(){
        return {
            activeName2: 'first',
            tableData:[],
            hotGoods:[],
            goodsList0:[],
            goodsList1:[],
            goodsList2:[],
            goodsList3:[],
            totalCount:0,
            totalMoney:0
        }
    },
    created: function(){
        this.getData({
            type:'hot',
            url:'http://jspang.com/DemoApi/oftenGoods.php'
        });
        this.getData({
            type:'good',
            url:'http://jspang.com/DemoApi/typeGoods.php'
        });
    },
    mounted:function(){
        var self = this;
        (function(factory){
            factory();
            window.addEventListener('resize',factory,false);
        })(function(){
            var bodyHeight = document.body.clientHeight;
            self.$("order-list").style.height = bodyHeight + 'px';
            self.$("goods-list").style.height = bodyHeight + 'px';
        })
    },
    methods:{
        $(elem){
            return document.getElementById(elem);
        },
        getData(obj) {
            
            axios.get(obj.url)
            .then(res => {
                if(obj.type === 'hot'){
                    this.hotGoods = res.data;
                }else if(obj.type === 'good'){
                    this.goodsList0 = res.data[0];
                    this.goodsList1 = res.data[1];
                    this.goodsList2 = res.data[2];
                    this.goodsList3 = res.data[3];
                }
            })
            .catch(res => {
                this.$message.error('网络错误，不能请求！');
                
                //假数据
                var json1 = {"data":[{"goodsId":1,"goodsName":"香辣鸡腿堡","price":18},{"goodsId":2,"goodsName":"田园鸡腿堡","price":15},{"goodsId":3,"goodsName":"和风汉堡","price":15},{"goodsId":4,"goodsName":"大包薯条","price":18},{"goodsId":5,"goodsName":"脆皮炸鸡腿","price":20},{"goodsId":6,"goodsName":"魔法鸡块","price":20},{"goodsId":7,"goodsName":"可乐大杯","price":10},{"goodsId":8,"goodsName":"雪顶咖啡","price":18},{"goodsId":9,"goodsName":"儿童欢乐套餐","price":25},{"goodsId":10,"goodsName":"快乐全家桶","price":99}],"status":200,"statusText":"OK","headers":{"content-type":"text/html; charset=UTF-8"},"config":{"transformRequest":{},"transformResponse":{},"timeout":0,"xsrfCookieName":"XSRF-TOKEN","xsrfHeaderName":"X-XSRF-TOKEN","maxContentLength":-1,"headers":{"Accept":"application/json, text/plain, */*"},"method":"get","url":"http://jspang.com/DemoApi/oftenGoods.php"},"request":{}};
                var json2 = {"data":[[{"goodsId":1,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg","goodsName":"香辣鸡腿堡","price":18},{"goodsId":2,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg","goodsName":"田园鸡腿堡","price":15},{"goodsId":3,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg","goodsName":"和风汉堡","price":15}],[{"goodsId":4,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg","goodsName":"大包薯条","price":18},{"goodsId":5,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg","goodsName":"脆皮炸鸡腿","price":20},{"goodsId":6,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg","goodsName":"魔法鸡块","price":20}],[{"goodsId":7,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg","goodsName":"可乐大杯","price":10},{"goodsId":8,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg","goodsName":"雪顶咖啡","price":18}],[{"goodsId":9,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg","goodsName":"儿童欢乐套餐","price":25},{"goodsId":10,"goodsImg":"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg","goodsName":"快乐全家桶","price":99}]],"status":200,"statusText":"OK","headers":{"content-type":"text/html; charset=UTF-8"},"config":{"transformRequest":{},"transformResponse":{},"timeout":0,"xsrfCookieName":"XSRF-TOKEN","xsrfHeaderName":"X-XSRF-TOKEN","maxContentLength":-1,"headers":{"Accept":"application/json, text/plain, */*"},"method":"get","url":"http://jspang.com/DemoApi/typeGoods.php"},"request":{}};

                if(obj.type === 'hot'){
                    this.hotGoods = json1.data;
                }else if(obj.type === 'good'){
                    this.goodsList0 = json2.data[0];
                    this.goodsList1 = json2.data[1];
                    this.goodsList2 = json2.data[2];
                    this.goodsList3 = json2.data[3];
                }
            })
        },
        delSingleGoods(goods){
            this.$confirm('确定要删除该商品？', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.tableData = this.tableData.filter(current => current.goodsId !== goods.goodsId);
                this.getTotalMoney();
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除!'
                });          
            });
        },
        addOrderList(goods){
            let isHave = false;
            this.tableData.forEach(function(current){
                if(current.goodsId === goods.goodsId){
                    isHave = true;
                    return;
                }
            });

            if(isHave){
                let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
                arr[0].count++;
            }else{
                let newObj = {
                    goodsId : goods.goodsId,
                    goodsName : goods.goodsName,
                    price : goods.price,
                    count : 1
                }
                this.tableData.push(newObj);
            }
            this.getTotalMoney();
        },
        getTotalMoney(){
            this.totalCount = 0;
            this.totalMoney = 0;
            this.tableData && this.tableData.forEach(current => {
                this.totalCount += current.count;
                this.totalMoney += current.count * current.price;
            })
        },
        delAllGoods(){
            if(this.totalCount !== 0){
                this.$confirm('确定要删除该订单？', '删除提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.totalCount = 0;
                    this.totalMoney = 0;
                    this.tableData = [];
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            }else{
                this.$message({
                    message: '没有可删除订单!',
                    type:'error'
                });
            }
        },
        checkOut(){
            if(this.totalMoney !== 0){
                this.totalCount = 0;
                this.totalMoney = 0;
                this.tableData = [];
                this.$message({
                    message: '结账成功！',
                    type: 'success'
                });
            }else{
                this.$message({
                    message: '请添加订单!',
                    type:'error'
                });
            }
        }
    }
}    
</script>
<style scoped>
    .total-content{
        text-align:center;
        font-size:16px;
        margin:0;
        height:50px;
        line-height:50px;
        color:brown;
        border-bottom:1px solid #D3DCE6;
    }
    .total-content small{color:#777;margin:0 10px;}
    .order-list{
        background-color:#fff;
        border-right:1px solid #aaa;
    }
    .goods-list{
        background-color:#fff;
    }
    #pos.pos{text-align:left;}
    .btn-group{
        text-align:center;
        margin-top:10px;
    }
    
    .title{
        height: 39px;
        line-height:39px;
        padding-left:10px;
        border-bottom:1px solid #D3DCE6;
        background-color: #f5f7fa;
    }
    .clear-float:after{
        content: '';
        display: block;
        height:0;
        clear:both;
        visibility:hidden;
    }
    .often-goods-list ul li{
        list-style: none;
        float:left;
        border:1px solid #E5E9F2;
        padding:10px;
        margin:5px;
        background-color:#fff;
        cursor: pointer;
    }
    .o-price{
        color:#58B7FF; 
    }

    .cookList li{
        list-style: none;
        width:23%;
        border:1px solid #E5E9F2;
        height: auot;
        overflow: hidden;
        background-color:#fff;
        padding: 2px;
        float:left;
        margin: 2px;
        cursor: pointer;
    }
    .cookList li span{
        display: block;
        float:left;
    }
    .foodImg{
        width: 40%;
    }
    .foodName{
        font-size: 18px;
        padding-left: 10px;
        color:brown;
        width:60%;
        box-sizing:border-box;
    }
    .foodPrice{
        font-size: 16px;
        padding-left: 10px;
        padding-top:10px;
        width:60%;
        box-sizing:border-box;
    }
</style>



